body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f3e5f5;
  color: #4a148c;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.view-toggle-button {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: #ce93d8;
  border: none;
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.view-toggle-button:hover {
  background-color: #ab47bc;
}

.graph-container, .image-container {
  width: 80%;
  max-width: 800px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.toggle-button {
  background-color: #ce93d8;
  border: none;
  color: white;
  padding: 10px;
  margin: 5px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.toggle-button:hover {
  background-color: #ab47bc;
}

h2 {
  text-align: center;
}

.description {
  margin-top: 20px;
  font-size: 14px;
  color: #333;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery-item {
  margin: 10px;
  text-align: center;
}

.gallery-item img {
  width: 150px;
  height: auto;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.gallery-item img:hover {
  transform: scale(1.05);
}

.gallery-item p {
  margin-top: 5px;
  font-size: 14px;
  color: #4a148c;
}

/* Modal styles */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 1000;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 80%;
  max-height: 80%;
  overflow: auto;
  z-index: 1001;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

.modal-image {
  width: 100%;
  height: auto;
}

.modal-close-button {
  display: block;
  margin: 20px auto 0;
  padding: 10px 20px;
  background-color: #ce93d8;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.modal-close-button:hover {
  background-color: #ab47bc;
}
